import { FitAddon } from "@xterm/addon-fit";
import React, { useEffect, useRef } from "react";
import { useXTerm } from "react-xtermjs";

function Terminal() {
  const { instance, ref } = useXTerm();
  const fitAddon = new FitAddon();

  const input = useRef("");
  const inputsArr = useRef([]);
  const xRef = useRef(0);
  const yRef = useRef(-1);

  const commandHandler = (input) => {
    switch (input.trim()) {
      case "help":
        print.command();
        break;
      case "info":
        print.myInfo();
        break;
      case "career":
        print.career();
        break;
      case "git":
        print.connect("git");
        window.open("https://www.github.com/sodra6", "_blank");
        break;
      case "blog":
        print.connect("blog");
        window.open("https://sodra6.tistory.com/", "_blank");
        break;
      case "exit":
        instance?.dispose();
        break;
      default:
        print.default();
        break;
    }
  };

  const print = {
    default: () => {
      instance?.write(
        "\r\n\x1b[33m  . help    "
      );
    },
    connect : (method) => {
      instance?.writeln(`\r\n\x1b[35m Connecting ${method !== 'git' ? 'blog':'Git repository'}...\x1b[0m`);
    },
    command: () => {
      instance?.writeln("\r\n\x1b[32m  : ");
      instance?.writeln(
        "\x1b[33m\x1b[1m help \x1b[0m:     ."
      );
      instance?.writeln(
        "\x1b[33m\x1b[1m info \x1b[0m:   ."
      );
      instance?.writeln(
        "\x1b[33m\x1b[1m career \x1b[0m:   ."
      );
      instance?.writeln(
        "\x1b[33m\x1b[1m blog \x1b[0m:  .()"
      );
      instance?.writeln(
        "\x1b[33m\x1b[1m git \x1b[0m : git  .()"
      );
      instance?.writeln("\x1b[33m\x1b[1m exit \x1b[0m:  .");
    },
    myInfo: (delay) => {
      let index = 0;
      const text = `
        name: 'yong gyu' | 'Cho',\r\n
        email: 'sodra6@naver.com',\r\n
        git: 'https://github.com/sodra6',\r\n
        keywords: ['Web Dev', 'Front-end', 'Back-end'],\r\n
        career : ['Mind-One cop/2021-2023', 'Biztech i/2023-ing'],\r\n
        technologies: {\r\n
            languages: ['Java', 'JavaScript', 'SQL', 'HTML', 'CSS','JSP'],\r\n
            web: [\r\n
                  'React', 'jQuery',\r\n
                  'Axios', 'Bootstrap', 'Materialize CSS',\r\n
                  'Spring MVC',\r\n
                ],\r\n
            databases: [\r\n
                  'PostgreSQL', 'MariaDB/MySQL'\r\n
                  'ORACLE', 'MySQL', 'MS-SQL',\r\n
                ],\r\n
            devOps: ['Git', 'Gitlab CI', 'SVN'],\r\n
            clouds: ['AWS'],\r\n
            operatingSystems: ['Linux', 'Unix', 'Windows'],\r\n
            editors: ['VSCode', 'Intellij', 'eclipse'],\r\n
            Education: ['Kyung-in high school', 'Dankook University Bachelor of Public Administration']\r\n
        \r\n
sodra6 $ `;
      const intervalId = setInterval(() => {
        if (index === 0)
          instance?.writeln("\r\n\x1b[34m Displaying information...\x1b[0m");
        if (index < text.length) {
          instance?.write(text[index]);
          index++;
        } else {
          clearInterval(intervalId);
        }
      }, delay);
    },
    career : (delay) => {
      let index = 0;
      const text = `
         : 'AI      '\r\n
         : 2021.08.16-2022.04.11\r\n
         : Spring framework, jquery, Tibero, Tensorflow\r\n
         : \r\n
           : \r\n
         : 
\r\n
        ----------------------------------------------------------------\r\n
\r\n
         : ' () '\r\n
         : 2022.04.01-2023.01.01\r\n
         : Spring boot, jquery, PostgreSQL, Tensorflow\r\n
         : \r\n
           : \r\n
         : 
\r\n
        ----------------------------------------------------------------\r\n
\r\n
         : '    '\r\n
         : 2023.01.01-2023.07.14\r\n
         : Spring boot, React, PostgreSQL\r\n
         : \r\n
           : \r\n
         : 
        \r\n
        ----------------------------------------------------------------\r\n
\r\n
         : 'Bizcare 3.0 '\r\n
         : 2023.07.17-2024.11.03\r\n
         : Spring framework, jquery, Ms-sql\r\n
         : Biztech i\r\n
           : Biztech i\r\n
         : 
\r\n
        ----------------------------------------------------------------\r\n
\r\n
         : 'LG CNS LG-NET  '\r\n
         : 2023.11.06-2024.12.31\r\n
         : Spring boot, React, MariaDB\r\n
         : Biztech i\r\n
           : LG CNS\r\n
         : 
\r\n

sodra6 $ `;
      const intervalId = setInterval(() => {
        if (index === 0)
          instance?.writeln("\r\n\x1b[34m Displaying information...\x1b[0m");
        if (index < text.length) {
          instance?.write(text[index]);
          index++;
        } else {
          clearInterval(intervalId);
        }
      }, delay);
    },
  };

  useEffect(() => {
    instance?.loadAddon(fitAddon);
    instance?.open(ref);
    fitAddon.fit();

    instance?.write("\r\n");
    instance?.write(" !  .");
    instance?.write("\r\n");
    instance?.write(
      "\r\n\x1b[33m       '\x1b[1mhelp\x1b[0m\x1b[33m'   .\x1b[0m"
    );
    instance?.write("\r\n");
    instance?.write("\r\n");
    instance?.write(" sodra6 $ ");

    instance?.onKey((e) => {
      if (e.domEvent.key === "Enter") {
        commandHandler(input.current);
        inputsArr.current.push(input.current.trim());
        yRef.current = inputsArr.current.length;

        input.current = ""; //  
        xRef.current = 0;
        instance?.write("\x1b[0m");
        instance?.write("\r\n");
        instance.write(" sodra6 $ ");
      } else {
        if (e.domEvent.key === "Backspace") {
          if (xRef.current > 0) {
            instance?.write("\x1B[1D"); // 
            instance?.write("\x1b[P"); //  
            input.current = input.current.slice(0, -1);
            xRef.current -= 1;
          }
        } else if (e.domEvent.key === "Delete") {
          if (input.current.length > 0 && xRef.current < input.current.length) {
            instance?.write("\x1B[P"); //  
            input.current = input.current.slice(0, xRef.current) + input.current.slice(xRef.current + 1)
            xRef.current = input.current.length - 1;
          }
        } else if (e.domEvent.key === "ArrowUp") {
          if(yRef.current > 0){
            yRef.current -= 1;
            input.current = inputsArr.current[yRef.current];
            xRef.current = input.current.length;
            instance?.write(input.current)
          }
        } else if (e.domEvent.key === "ArrowDown") {
          if (yRef.current < inputsArr.current.length - 1) {
            yRef.current += 1;
            input.current = inputsArr.current[yRef.current];
            xRef.current = input.current.length;
          } else {
            yRef.current = inputsArr.current.length;
            input.current = "";
            xRef.current = input.current.length;
          }
          instance?.write(input.current)
        } else if (e.domEvent.key === "ArrowLeft") {
          if (input.current.length > 0 && xRef.current > 0) {
            xRef.current -= 1;
            instance?.write("\x1b[D");
          }
        } else if (e.domEvent.key === "ArrowRight") {
          console.log(xRef.current)
          console.log(input.current.length)
          if (input.current.length > 0 && xRef.current < input.current.length-1) {
            xRef.current += 1;
            instance?.write("\x1b[C");
          }
        } else {
          instance?.write(e.key);
          input.current += e.key;
          xRef.current += 1;
        }
      }
      return () => {
        instance?.dispose();
      };
    });
  }, [instance]);


  return (
    <div
      className="terminal-container"
      style={{
        height: "100vh",
        width: "100%",
        backgroundColor: "black",
      }}
      ref={ref}
    />
  );
}

export default Terminal;
